@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,700;1,300;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{background:#DB75C5;height:80px;display:flex;justify-content:center;align-items:center;font-size:1.2rem;position:-webkit-sticky;position:sticky;top:0;z-index:999}.navbar-container{width:1080px;min-width:1080px;display:flex;justify-content:space-between;height:80px}.navbox{z-index:1;width:100%;margin-right:auto;margin-left:auto;padding-right:50px;padding-left:50px}.navbar-logo{color:#fff;justify-self:start;cursor:pointer;text-decoration:none;font-size:2rem;display:flex;align-items:center}.nav-menu{display:flex;align-items:center;justify-content:center;list-style:none;text-align:center;justify-content:felx-end}.nav-item{height:80px;border-bottom:2px solid transparent}.nav-item:hover{border-bottom:2px solid #6A1051}.nav-links{color:#fff;display:flex;align-items:center;text-decoration:none;padding:0.5rem 1rem;height:100%}.fa-bars{color:#fff}.menu-icon{display:none}@media screen and (max-width: 960px){.NavbarItems{position:relative}.nav-menu{display:flex;flex-direction:column;width:100%;height:90vh;position:absolute;top:80px;left:-100%;opacity:1;transition:all 0.5s ease}.nav-menu.active{background:#DB75C5;left:0;opacity:1;transition:all 0.6s ease;z-index:1}.nav-links{text-align:center;padding:2rem;width:100%;display:table}.nav-links:hover{color:#6A1051;transform:scale(1.2);transition:all 0.3s ease}.nav-item:hover{border:none}.nav-item{width:100%}.navbar-logo{position:absolute;top:0;left:0;transform:translate(25%, 50%)}.menu-icon{display:block;position:absolute;top:0;right:0;transform:translate(-100%, 60%);font-size:1.8rem;cursor:pointer}.fa-times{color:#fff;font-size:2rem}}.banner{display:flex;justify-content:center;min-height:50vh;min-width:100%;background-image:linear-gradient(rgba(150,150,150,0.3), rgba(150,150,150,0.3)),url("https://res.cloudinary.com/daf8fgekl/image/upload/v1597908882/gozero_pu9y9b.png");background-repeat:no-repeat;background-attachment:fixed;background-position:center}.banner .banner-text{align-self:center}.banner .banner-text h1{color:#fff;font-size:48px;margin:0;text-shadow:2px 2px #000}.banner .banner-btn{background:#6A1051;border:1px solid #fff;border-radius:8px;padding:15px 32px;text-align:center;text-decoration:none;display:inline-block;font-size:16px}.banner .banner-btn a{color:#fff;text-decoration:none}.banner .banner-btn:hover{background:#DB75C5;color:#6A1051;outline:0;transition:color, background-color 3s ease-out}.footer{width:100%;min-height:4vh;margin-top:auto;display:flex;justify-content:space-around;align-items:center;background:#DB75C5}.footer .impressum{display:flex;flex-direction:column;align-items:center;padding:12px;font-size:12px;list-style:none;color:#fff}.tweets{display:flex;flex-direction:column}.tweets .tweet{display:flex;justify-content:space-around;align-item:center}.tweets .tweet .tweet-text{width:50%;margin:auto;padding:32px}.tweets .tweet .tweet-text p{letter-spacing:1px;line-height:1.2}.tweets .tweet img{width:400px;height:400px;border-radius:50%;margin:3% auto}.about{display:flex;justify-content:space-around}.about .about-text{width:50%;margin:auto;padding-top:48px}.about .about-text p{letter-spacing:1px;line-height:1.2}.about img{width:500px;height:500px;border-radius:50%;margin:2% auto}*{box-sizing:border-box;margin:0;padding:0}body,html{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;background:#FFF5DB;font-family:"Josefin Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.container{width:1560px;min-width:1560px;margin:0 auto;min-height:100%;background:#FFF5DB}@media (max-width: 1560px){.container{width:1280px;min-width:1280px}}@media (max-width: 1366px){.container{width:1080px;min-width:1080px}}@media (max-width: 1080px){.container{width:100%;min-width:100%}}

